h1, h2, h3, h4, h5 {
  font-family: 'Bungee Outline', cursive;
  text-shadow: 3px -3px #e74c3c, -3px 3px #3498db;
}

p {
  font-family: 'Open Sans', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button {
  padding: 10px 30px;
  margin: 10px;
  border: 2px solid #FFE57B;
  /* box-shadow: 0px 0px 5px #A3FF53; */
  background-color: transparent;
  color: #FFE57B;
  border-radius: 5px;
  font-size: 30px;
  font-family: 'Lemonada', cursive;
  font-weight: 700;
  width: calc(100% - 20px);
  cursor: pointer;
  transition: 0.3s;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);  
}

button:hover {
  background-color: rgba(255, 255, 255, 0.1);
  box-shadow: 0px 0px 10px #FFE57B;
}

input {
    font-size: 30px;
    margin: 10px;
    padding: 10px 30px;
    text-align: center;
    border: 2px solid #A3FF53;
    border-radius: 5px;
    color: white;
    background-color: transparent;
    width: calc(100% - 20px);
    box-sizing: border-box;
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);    
}

input:focus {
    outline: none;  
}

.redText {
    color: #e74c3c;
}

.blurredBackground {
  background-color: rgba(255,255,255,0.1);
  padding: 10px 0px;
  margin: 20px 0px;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);  
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

